@include font('SFUIText-Regular', 'SFUIText-Regular');
@include font('SFUIText-Medium', 'SFUIText-Medium');
@include font('SFUIText-Light', 'SFUIText-Light');
@include font('SFUIText-Bold', 'SFUIText-Bold');

// fonts
$default-fonts: Arial, sans-serif;
@mixin sfui {
	font-family: 'SFUIText-Regular', $default-fonts; }
@mixin sfui-medium {
	font-family: 'SFUIText-Medium', $default-fonts; }
@mixin sfui-light {
	font-family: 'SFUIText-Light', $default-fonts; }
@mixin sfui-bold {
	font-family: 'SFUIText-Bold', $default-fonts; }

// colors
// $black: #000

@mixin trs {
	transition: all 0.25s; }

@mixin smooth {
	-webkit-font-smoothing: antialiased; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	position: relative;
	min-width: 1280px;
	@include sfui-medium;
	font-size: 21px;
	color: #333;
	line-height: 1.2;
	overflow: auto; }

.body-main {
	overflow: hidden; }

.wrapper {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	height: 100%;
	width: 100%;
	z-index: 1; }

.wrapper-default {
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%; }

.container {
	@include pie-clearfix; }


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

* {
	@include selection {
		color: #fff;
		background-color: #1176ae; } }



/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: fixed;
	z-index: 101;
	top: 0;
	left: 270px;
	right: 260px;
	padding-top: 15px;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	&_contact {
		right: 20px; }
	@include r(1919) {
		padding-top: 20px;
		.icon-phone {
			@include sr(phone); }
		.icon-present {
			@include sr(present); }
		.icon-lock {
			@include sr(lock); }
		.icon-user {
			@include sr(user); } }
	@include r(1599) {
		left: 220px; }
	&__item {
		font-size: 0;
		@include trs;
		&:hover {
			color: #1176ae; }
		&-icon {
			position: relative;
			margin-right: 25px;
			@include r(1919) {
				margin-right: 10px; } }
		&-counter {
			position: absolute;
			z-index: 1;
			top: -2px;
			right: -2px;
			padding-left: 3px;
			padding-right: 3px;
			height: 18px;
			min-width: 18px;
			font-size: 12px;
			color: #ffffff;
			line-height: 15px;
			text-align: center;
			border-radius: 50%;
			border: 2px solid #ffffff;
			background: #1176ae;
			@include r(1919) {
				border-width: 1px;
				line-height: 17px;
				top: -10px;
				right: -9px; } }
		&-text {
			display: inline-block;
			vertical-align: middle;
			font-size: 21px;
			& & {
				@include r(1439) {
					display: none; } }
			@include r(1919) {
				font-size: 18px; }
			@include r(1599) {
				font-size: 16px; }
			& + & {
				@include sfui; } }
		&_phone {
			transform: translateY(0);
			opacity: 1;
			transition: all 0.25s, transform 0.4s, opacity 0.4s;
			&.is-active {
				opacity: 0;
				transform: translateY(-150%); } } } }


/* ===================================
	 Aside
	 @mixin ================================== */
.aside {
	overflow: auto;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	z-index: 102;
	top: 0;
	bottom: 0;
	height: 100%;
	left: 0;
	width: 190px;
	background-color: rgba(255, 255, 255, .75);
	@include r(1599) {
		width: 140px; }
	&__logo {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1599) {
			margin: 15px 0; }
		@include r(1599) {
			width: 70px; } }
	&__socials {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1599) {
			margin: 15px 0; } }
	&__geo {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1599) {
			margin: 15px 0; }
		@include s(geo); }
	&__arc {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1919) {
			margin: 15px 0; }
		@include r(1599) {
			margin: 5px 0; }
		@include s(arc); } }

.socials {
	&__link {
		display: block;
		& + & {
			margin-top: 12px; }
		&:nth-child(1) {
			@include s(soc-1); }
		&:nth-child(2) {
			@include s(soc-2); }
		&:nth-child(3) {
			@include s(soc-3); }
		&:nth-child(4) {
			@include s(soc-4); }
		&:nth-child(5) {
			@include s(soc-5); } }
	&_line {
		display: flex;
		flex-direction: row;
		justify-content: center; }
	&_line &__link + &__link {
		margin-top: 0;
		margin-left: 12px; } }

.nav {
	position: relative;
	padding-bottom: 30px;
	text-align: center;
	text-transform: uppercase;
	@include r(1919) {
		padding-bottom: 15px; }
	&:before {
		content: '';
		position: absolute;
		z-index: 1;
		width: 80px;
		height: 1px;
		left: 0;
		right: 0;
		bottom: 0;
		margin-left: auto;
		margin-right: auto;
		background-color: #333333; }
	&__link {
		display: block;
		line-height: 1.67;
		@include trs;
		@include r(1919) {
			font-size: 18px;
			line-height: 1.4; }
		@include r(1599) {
			font-size: 16px; }
		@include r(1439) {
			font-size: 15px; }
		&:hover {
			color: #1176ae; }
		& + & {
			@include sfui; } } }

#pp-nav {
	&.right {
		top: auto;
		right: 220px;
		bottom: 40px;
		@include r(1919) {
			bottom: 10px; }
		li {
			margin: 0;
			width: auto;
			height: auto;
			+ li {
				margin-top: 7px; } }
		span {
			position: relative;
			display: block;
			width: 18px;
			height: 18px;
			top: auto;
			left: auto;
			border-width: 3px;
			@include trs;
			@include r(1919) {
				border-width: 2px;
				width: 14px;
				height: 14px; } }
		.active {
			span {
				background: #ffffff; } } } }

/* ===================================
	 Navigation
	 @mixin ================================== */
.navigation {
	overflow: auto;
	display: none;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	z-index: 103;
	top: 0;
	bottom: 0;
	left: 0;
	width: 380px;
	background-color: rgba(255, 255, 255, .75);
	transform: translateX(-190px);
	transition: transform 0.4s;
	@include r(1599) {
		width: 300px;
		transform: translateX(-160px); }
	&.is-active {
		transform: translateX(0); }
	&__logo {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1599) {
			margin: 15px 0; }
		@include r(1599) {
			width: 170px; } }
	&__arc {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1599) {
			margin: 15px 0; }
		@include sfui;
		font-size: 13px;
		@include trs;
		&:before {
			content: '';
			display: block;
			@include s(arc-black);
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 10px; }
		&:hover {
			color: #1176ae; }
		&-color {
			display: block;
			text-align: center;
			@include sfui-medium; } }
	&__phone {
		flex-shrink: 0;
		margin: 25px 0;
		text-align: center;
		@include trs;
		@include r(1599) {
			margin: 15px 0; }
		&:hover {
			color: #1176ae; }
		&-text {
			font-size: 24px;
			display: block;
			@include r(1919) {
				font-size: 20px; }
			& + & {
				@include sfui;
				font-size: 18px;
				@include r(1919) {
					font-size: 14px; } } } }
	&__address {
		flex-shrink: 0;
		margin: 5px 0;
		text-align: center;
		font-size: 18px;
		@include r(1599) {
			margin: 0; } }
	&__socials {
		flex-shrink: 0;
		margin: 25px 0;
		@include r(1599) {
			margin: 5px 0; } } }


/* ===================================
	 Control
	 @mixin ================================== */
.control {
	display: flex;
	flex-direction: column;
	position: fixed;
	z-index: 102;
	top: 0;
	right: 0;
	bottom: 0;
	height: 100%;
	width: 195px;
	border-left: 5px solid #ffffff;
	&:before {
		content: '';
		position: absolute;
		right: 100%;
		margin-right: 5px;
		top: 20%;
		margin-top: 60px;
		@include arr(15,30,#ffffff,l); }
	@include r(1439) {
		width: 160px; }
	&__item {
		display: none;
		cursor: pointer;
		flex: 1;
		flex-direction: column;
		align-items: center;
		padding: 0 10px 0 30px;
		justify-content: center;
		position: relative;
		overflow: hidden;
		@include r(1919) {
			padding-left: 20px; }
		@include r(1439) {
			padding-left: 10px; }
		&-text {
			margin: 10px 0;
			position: relative;
			z-index: 3;
			color: #ffffff;
			width: 100%;
			order: 1;
			@include r(1919) {
				margin: 5px 0;
				font-size: 18px; }
			@include r(1439) {
				margin: 0;
				font-size: 16px; } }
		&-icon {
			position: relative;
			order: 2;
			z-index: 3;
			align-self: flex-start;
			margin: 10px 0;
			transition: transform 0.4s; }
		&:hover &-icon {
			transform: translateX(55px);
			@include r(1919) {
				transform: translateX(65px); }
			@include r(1439) {
				transform: translateX(55px); } }
		&:before {
			pointer-events: none;
			content: '';
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, .65);
			transition: all 0.4s;
			border-top: 1px solid rgba(255, 255, 255, .4); }
		&:after {
			content: '';
			pointer-events: none;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
			background-repeat: no-repeat;
			background-position: center;
			background-color: #fff;
			background-size: cover;
			transition: all 0.4s; }
		&:nth-child(1):after {
			background-image: url(../img/bg/section-1-sm.jpg); }
		&:nth-child(2):after {
			background-image: url(../img/bg/section-2-sm.jpg); }
		&:nth-child(3):after {
			background-image: url(../img/bg/section-3-sm.jpg); }
		&:nth-child(4):after {
			background-image: url(../img/bg/section-4-sm.jpg); }
		&:nth-child(5):after {
			background-image: url(../img/bg/section-5-sm.jpg); }
		&:hover:before {
			background-color: rgba(0, 0, 0, .25); }
		&:hover:after {
			transform: scale(1.2); }
		&_prev {
			display: flex;
			order: 1; }
		&_prev &-icon {
			order: 0;
			@include s(arrow-prev); }
		&_current {
			cursor: auto;
			overflow: visible;
			flex: 2;
			display: flex;
			order: 2;
			transition: none;
			&:before {
				display: none; }
			&:hover:before {
				display: none; }
			&:hover:after {
				transform: none;
				transition: none; } }
		&_current &-text,
		&_current &-icon {
			display: none; }
		&_next {
			display: flex;
			order: 3; }
		&_next &-icon {
			@include s(arrow-next); }
		&_last {
			display: flex;
			order: 4; }
		&_last &-icon {
			@include s(arrow-next-double); } } }


/* ===================================
	 Main
	 @mixin ================================== */
.preload-img {
	display: none!important;
	width: 0;
	height: 0;
	visibility: hidden; }

.section {
	width: calc(100% - 195px);
	top: 0;
	left: 0;
	background-attachment: fixed;
	transition: all 1s cubic-bezier(.55,.085,0,.99);
	@include r(1439) {
		width: calc(100% - 160px); }
	&__bg {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		pointer-events: none;
		background-repeat: no-repeat;
		background-position: center;
		background-color: #ffffff;
		background-size: cover;
		& + & {
			display: none;
			z-index: 2; } }
	&_1 &__bg {
		background-image: url(../img/bg/section-1.jpg); }
	&_2 &__bg {
		background-image: url(../img/bg/section-2.jpg); }
	&_3 &__bg {
		background-image: url(../img/bg/section-3.jpg); }
	&_4 &__bg {
		background-image: url(../img/bg/section-4.jpg); }
	&_5 &__bg {
		background-image: url(../img/bg/section-5.jpg); } }

.pp-tableCell {
	position: relative;
	z-index: 2; }

/* Content
	----------------------------------- */
.content {
	position: absolute;
	z-index: 3;
	right: 0;
	top: 0;
	bottom: 0;
	left: 190px;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	@include r(1599) {
		left: 140px; }
	&__inner {
		display: flex;
		flex-direction: row;
		margin-top: 20vh;
		flex-shrink: 1;
		flex-grow: 0;
		flex-basis: calc(40% + 1px);
		flex-wrap: wrap;
		justify-content: flex-end; }
	.slick-list,
	.slick-track {
		height: 100% !important; }
	.slick-prev {
		position: absolute;
		z-index: 10;
		top: 0;
		right: 100%;
		height: 50%;
		width: 96px;
		background-color: rgba(255, 255, 255, .75);
		@include vertical;
		@include trs;
		@include r(1919) {
			width: 64px; }
		@include r(1439) {
			width: 48px; }
		&:hover {
			background-color: #ffffff; }
		.icon {
			display: inline-block;
			vertical-align: middle; } }
	.slick-next {
		position: absolute;
		z-index: 10;
		top: 50%;
		right: 100%;
		height: 50%;
		width: 96px;
		background-color: rgba(255, 255, 255, .75);
		@include vertical;
		@include trs;
		@include r(1919) {
			width: 64px; }
		@include r(1439) {
			width: 48px; }
		&:hover {
			background-color: #ffffff; }
		.icon {
			display: inline-block;
			vertical-align: middle; } }
	&__left {
		position: relative;
		width: 570px;
		height: 40vh !important;
		display: flex !important;
		flex-direction: row;
		flex-wrap: wrap;
		background-color: rgba(0, 0, 0, .2);
		@include r(1919) {
			width: 510px; }
		@include r(1439) {
			width: 450px; } }
	&__slider {
		width: 570px;
		height: 40vh;
		@include r(1919) {
			width: 510px; }
		@include r(1439) {
			width: 450px; } }
	&__block {
		cursor: pointer;
		position: relative;
		width: 33.33333%;
		height: 50%;
		@include sfui;
		color: #ffffff;
		background-size: cover;
		border-left: 1px solid rgba(0, 0, 0, .4);
		border-top: 1px solid rgba(0, 0, 0, .4);
		&:last-child {
			background: #3876ae;
			@include r(1919) {
				font-size: 18px; } }
		&:nth-child(3) ~ & {
			border-bottom: 1px solid rgba(0, 0, 0, .4); }
		&:nth-child(3) ~ &:last-child {
			border: 1px solid rgba(0, 0, 0, .1); }
		&:before {
			pointer-events: none;
			content: '';
			position: absolute;
			z-index: 1;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, .5);
			transition: all 0.4s; }
		&:hover:before {
			background-color: rgba(255, 255, 255, .75); }
		&:last-child {
			border: none;
			&:before {
				display: none;
				content: ''; } }
		&-in {
			display: block;
			position: relative;
			z-index: 2;
			padding-left: 15px;
			height: 100%;
			padding-top: 35%;
			transition: all 0.4s;
			@include r(1919) {
				padding-top: 25%; } }
		&-title {
			font-size: 27px;
			@include r(1919) {
				font-size: 22px; }
			@include r(1439) {
				font-size: 18px; } }
		&-price {
			@include r(1919) {
				font-size: 18px; }
			@include r(1439) {
				font-size: 14px; } }
		&-desc {
			position: absolute;
			z-index: 3;
			pointer-events: none;
			color: #333333;
			padding: 9px 50px 9px 15px;
			border: 2px solid #333333;
			opacity: 0;
			top: 50%;
			transform: translateY(-25%);
			left: 15px;
			right: 15px;
			transition: all 0.4s;
			@include r(1919) {
				font-size: 18px; }
			@include r(1439) {
				font-size: 15px; } }
		&:hover &-in {
			opacity: 0;
			&:last-child {
				opacity: 1; } }
		&:hover &-desc {
			opacity: 1;
			transform: translateY(-50%); }
		&-text-border {
			position: relative;
			&:before {
				position: absolute;
				content: '';
				z-index: 1;
				left: 0;
				right: 0;
				bottom: -5px;
				width: 0;
				height: 1px;
				background-color: #ffffff;
				transition: all 0.4s; } }
		&:hover &-text-border:before {
			width: 60%; } }
	&__main {
		width: 670px;
		padding-top: 30px;
		padding-right: 50px;
		@include sfui;
		text-align: right;
		background-color: rgba(255, 255, 255, .75);
		border: 1px solid rgba(0, 0, 0, .1);
		border-right: none;
		@include r(1919) {
			padding-right: 30px;
			padding-top: 20px;
			width: 420px; }
		&-title {
			font-size: 84px;
			text-transform: uppercase;
			@include r(1919) {
				font-size: 48px; }
			&_sm {
				font-size: 44px;
				@include r(1919) {
					font-size: 27px; } } }
		&-text {
			margin-top: 10px;
			font-size: 45px;
			@include r(1919) {
				font-size: 32px; }
			&_sm {
				margin-top: 5px;
				font-size: 42px;
				@include r(1919) {
					font-size: 24px; } } }
		&-text + &-title {
			margin-top: 50px; } }
	&__row-sub {
		flex-basis: 100%;
		height: 20vh;
		display: flex;
		justify-content: flex-end; }
	&__sub {
		width: 670px;
		height: 100%;
		padding-top: 35px;
		padding-right: 50px;
		padding-left: 50px;
		@include sfui;
		font-size: 25px;
		color: #ffffff;
		text-align: right;
		background-color: rgba(0, 0, 0, .5);
		@include r(1919) {
			width: 420px;
			padding-top: 20px;
			padding-right: 30px;
			font-size: 22px; }
		&-text {
			&-sm {
				@include sfui-medium;
				font-size: 21px;
				@include r(1919) {
					font-size: 18px; }
				@include r(1439) {
					font-size: 16px; } } } }
	&__desc {
		width: 380px;
		margin-right: 13px;
		&-look {
			display: block;
